import BlogCard from "@/app/components/homeComps/BlogCard";
import { Fragment } from "react";
import { cardData } from "@/app/components/homeComps/BlogCard";
import { Avatar, AvatarFallback, AvatarImage } from "@radix-ui/react-avatar";

export default function Blog() {
  return (
    <Fragment>
      <section className="  flex flex-col max-md:flex-col items-center h-[50vh] max-md:pt-[70px] px-[5%] justify-evenly bg-black   ">
        <div className="md:left md:w-[70%]">
          <h2 className="text-white text-[60px] text-center max-md:text-[24px] md:leading-[70px] ">
            Our Blog
          </h2>
        </div>
        <div className="right flex flex-col max-md:justify-center max-md:text-center items-center ">
          <h4 className="text-[#a5acc0] text-[20px] max-md:text-[16px]  pb-10">
            Insights, ideas, and inspiration for modern marketers, designers,
            and developers.
          </h4>
        </div>
      </section>

      {/* Featured Articles */}
      <section className="  flex flex-col max-lg:flex-col items-center  px-[5%]  justify-evenly bg-black   ">
        <h2 className="text-white text-xl w-full text-start font-semibold mb-4 ">
          Feature Articles
        </h2>

        <div className="flex flex-row flex-wrap gap-1 justify-between">
          {cardData.map((card, index) => {
            {
              return !card.featured ? (
                ""
              ) : (
                <div
                  key={index}
                  className="w-[300px] max-lg:w-[45%] max-md:w-full  group  flex flex-col items-center justify-between pb-4  cursor-pointer rounded-[20px] overflow-hidden border border-[#08090c]  transition-colors duration-300 ease-in-out hover:border-[#0a49eb] text-white  "
                >
                  <Avatar className=" w-full h-auto transition-transform rounded-none duration-300 ease-in-out group-hover:scale-105">
                    <AvatarImage
                      src={card.image}
                      alt="Service Card 1"
                      className="lg:h-[200px] object-cover rounded-none  "
                    />
                    <AvatarFallback>Card {index}</AvatarFallback>
                  </Avatar>
                  <div className="p-6 space-y-2 border border-transparent border-b-[#08090c]">
                    <h4 className="lg:text-[16px]   font-[600]">
                      {card.title}
                    </h4>
                    <p className="lg:text-[14px]  font-[400] ">{card.text}</p>
                  </div>
                  {card.button}
                  <div className="flex flex-row gap-x-2 justify-start  items-center">
                    <Avatar>
                      <AvatarImage
                        src={card.icon}
                        alt="Team Member"
                        className="w-10 h-10 rounded-full object-cover grayscale"
                      />
                      <AvatarFallback>Team Member</AvatarFallback>
                    </Avatar>

                    <div className="">
                      <h4>{card.name}</h4>
                      <h4>{card.date}</h4>
                    </div>
                  </div>
                </div>
              );
            }
          })}
        </div>
      </section>


      {/* Websites */}
       <section
              key={index}
              style={{ backgroundImage: `url(${bg.bg})` }}
              className={` bg-${bg.bgColor} bg-cover bg-center  bg-no-repeat flex flex-col items-center justify-center pb-[20px] lg:px-[5%] lg:px-12`}
            >
              <div className="  w-full max-lg:space-y-10 flex max-lg:flex-row   grid lg:grid-cols-2  items-center">
                {/* Left Text Content */}
                <div className=" flex flex-col   max-lg:justify-evenly max-lg:pt-[50px] lg:justify-between px-[5%]">
                  <h1
                    className={`text-5xl max-lg:text-[24px] font-bold leading-tight text-${bg.textColor} `}
                  >
                    We craft fast, scalable sites for teams of all sizes.
                  </h1>

                  <h4
                    className={`font-normal text-${bg.textColor} text-[20px] max-lg:text-[16px] lg:leading-[30px] `}
                  >
                    Webstacks empowers marketing teams to break free from
                    website bottlenecks with a composable approach built for
                    speed and scale.
                  </h4>

                  <div className="flex max-lg:flex-col gap-4 max-lg:w-full">
                    <WSButton
                      icon={<ArrowUpRight />}
                      value="Book Intro Call"
                    ></WSButton>
                    <Button className="lg:p-7 text-base flex items-center gap-2 lg:text-[18px] max-lg:text-[14px]   max-lg:p-6">
                      View Client Stories
                      <ArrowRight className="w-5 h-5 max-lg:w-2 max-lg:h-2 text-white" />
                    </Button>
                  </div>
                </div>

                {/* Right Image */}
                <div
                  initial={{ rotateX: 0, rotateY: 0 }}
                  animate={{ rotateX: 360, rotateY: 360 }}
                  transition={{ duration: 2, ease: "easeInOut" }}
                  className=" border   rounded-[10px] lg:h-[70%]  bg-white "
                >
                  <div className=" flex flex-row text-[10px] h-[36px] border-b-1 justify-between items-center bg-white px-4 rounded-t-sm">
                    <div className="flex flex-row space-x-1 ">
                      <Circle
                        className={`lg:w-4 lg:h-4 max-lg:w-3 max-lg:h-3 bg-gray-200 text-gray-200 rounded-[10px]`}
                      />
                      <Circle className="w-4 h-4 max-lg:w-3 max-lg:h-3 bg-gray-200 text-gray-200 rounded-[10px]" />
                      <Circle className="w-4 h-4 max-lg:w-3 max-lg:h-3 bg-gray-200 text-gray-200 rounded-[10px]" />
                    </div>
                    <div className="flex justify-center   w-full">
                      <Link />
                    </div>
                  </div>
                  <div className="overflow-hidden w-full flex flex-col justify-center  scrollbar-hide">
                    <Avatar className="mad-lg:h-125">
                      <Image
                        src="/images/heroScrolls/scroll-1.jpg"
                        alt="Scrolling image"
                        className="object-contain"
                        initial={{ y: 0 }}
                        animate={animateMobile ? { y: 0 } : { y: -2400 }}
                        transition={{
                          duration: 10,
                          ease: "easeInOut",
                          repeat: Infinity,
                          repeatType: "reverse",
                        }}
                      />
                    </Avatar>
                  </div>
                </div>
              </div>
            

            </section>
    </Fragment>
  );
}
